const IDTYPE = { areaId: 0, indicatorId: 1, milieuId: 2 };

const App = () => {
  //what chart to display
  const [graphToDisplay, setGraphToDisplay] = useState("");
  //reference the svg element in the dom that we use to display chart/maps
  const svgRef = useRef();
  //keep state of the cities, indicators and milieus that the user select
  const [selectedParameters, setSelectedParameters] = useState({
    selectedAreas: [],
    selectedIndicators: [],
    selectedMilieu: [],
  });

  //to retreive selected Parameters from child (ParametersSideMenu)
  const retreiveSelectedParameters = (tempCollection, collectionType) => {
    if (collectionType == IDTYPE.areaId) {
      setSelectedParameters((currentParameters) => {
        return { ...currentParameters, selectedAreas: tempCollection };
      });
    }
    if (collectionType == IDTYPE.indicatorId) {
      setSelectedParameters((currentParameters) => {
        return { ...currentParameters, selectedIndicators: tempCollection };
      });
    }
    if (collectionType == IDTYPE.milieuId) {
      setSelectedParameters((currentParameters) => {
        return { ...currentParameters, selectedMilieu: tempCollection };
      });
    }
    //setSelectedParameters(params);
  };

  //each time a user clicks on a checkbox, we try to render
    useEffect(()=>{
    setSelectedParameters({
        selectedAreas: [
          "MA-AZI",
          "MA-BES",
          "MA-BER",
          "MA-BRR",
          "MA-BOD",
          "MA-BOM",
          "MA-BEM",
        ],
        selectedIndicators: ["Population Marocains"],
        selectedMilieu: ["Urbain"],
      });
  },[])
  useEffect(() => {
    //only render if atleast one each category is present

    if (
      selectedParameters.selectedIndicators.length > 0 &&
      selectedParameters.selectedAreas.length > 0 &&
      selectedParameters.selectedMilieu.length > 0
    ) {
      const svg = d3.select(svgRef.current);
      if (graphToDisplay == "Map") {
        ChoroplethMap(svg, geoData, areaData, data, selectedParameters);
      } else if (graphToDisplay == "Bar") {
        let barChartData = [];
        selectedParameters.selectedAreas.map((area) => {
          let obj = {};
          obj.name = areaData
            .filter((d) => d[1] === area)[0][2]
            .toLocaleString();
          obj.value = +data.ObservationData.filter(
            (it) =>
              (it[1].search("Province") === 0 ||
                it[1].search("Prfecture") === 0) &&
              it[4] === selectedParameters.selectedIndicators[0] &&
              it[6] === selectedParameters.selectedMilieu[0]
          ).find((it) => it[2] === area)?.[9];
          barChartData.push(obj);
        });

        BarChart(
          svg,
          barChartData,
          selectedParameters.selectedIndicators[0],
          (d) => d.name,
          (d) => d.value
        );
      }
    } else {
      d3.select('.svg').attr('fill','red')
    }
  }, [selectedParameters, graphToDisplay]);
  return (
    <Container fluid>
      <Row className="d-flex justify-content-around">
        <Col>
          <button
            className="btn btn-secondary"
            onClick={(ev) => setGraphToDisplay("Map")}
          >
            Map
          </button>
        </Col>
        <Col>
          <button
            className="btn btn-primary"
            onClick={(ev) => setGraphToDisplay("Bar")}
          >
            BarChart
          </button>
        </Col>
      </Row>
      <Row>
        <Col md={3}>
          <ParametersSideMenu
            data={areaData}
            selectedParameters={selectedParameters}
            retreiveSelectedParameters={retreiveSelectedParameters}
          ></ParametersSideMenu>
        </Col>
        <Col md={9}>
          <svg
            className="svg"
            height="90vh"
            viewBox="0 0 950 950"
            ref={svgRef}
          ></svg>
        </Col>
      </Row>
    </Container>
  );
};

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
